  oMessage = "";
  async loadQDetails() {
    this.fetchAppointments();
    this.oAPIService.send2ServerP("lqn/" + this.oEntity.ID + "/" + this.oService.ID, true).then((data: any) => {
      if (data.result.length == 0) {
        this.oMessage = "Closed";

        this.bLoaded = true;
      }
      else {
        if (data.result.length != 1) {
          console.log("FATAL Logic Error");
          console.log(JSON.stringify(data.result));
          //          this.fetchAppointments();
          this.bLoaded = true;
          return;
        }
        this.oItem = data.result[0];
        //        this.oItem.ENTITYID = 1 ;

        this.oItem.CURRENTQN_T = this.padLeft(this.oItem.CURRENTQN, '0', 3);
        this.oItem.ISSUEDQN_T = this.padLeft(this.oItem.ISSUEDQN, '0', 3);
        if (this.bDebug)
          console.log("*** SERVICE " + JSON.stringify(this.oService));
        this.oItem.PREFIX = this.oService.PREFIX;
        //        this.oItem.COUNTERN = this.l.counter;
        //        this.publish ( "LEDSetup/" + this.oItem.ENTITYID + "/" + this.oItem.SERVICEID,  this.oItem.COUNTERN + ",On") ;
        if (this.bDebug)
          console.log(JSON.stringify(this.oItem));
        // this.bHasMore = Number(this.oItem.CURRENTQN) < Number(this.oItem.ISSUEDQN);
        // this.bHasMore = true ;
        //        this.fetchAppointments();
        this.setConnect(true);
        super.subscribe("CQ/" + this.oItem.ENTITYID + "/" + this.oItem.SERVICEID);
        super.subscribe("AQ/" + this.oItem.ENTITYID + "/" + this.oItem.SERVICEID);
        this.bLoaded = true;
      }
    });
    // });

  }
  oAppointments = [];

  shouldDisable() {
    if (this.bConnected == false) return true;
    if (this.oAppointments.length > 0) {
      return false;
      // if (Number(this.oAppointments[0].AQNUMBER) <= Number(moment().format("HHmm")))
      //   return true;
    }
    return !(Number(this.oItem.CURRENTQN) < Number(this.oItem.ISSUEDQN));
  }

  async fetchAppointments() {
    this.oAPIService.send2ServerP("services/appointments/list/" + this.oEntity.ID
      + "/" + this.oService.ID + "", true).then((data: any) => {
        if (data.result._error)
          console.log(data.result._error);
        else
          this.oAppointments = data.result;
      });
  }
  ngOnInit(): void {
    // ionViewDidEnter() {
    this.oAppointments = [];
    if (this.bLoaded) {
      this.fetchAppointments();
      super.subscribe("CQ/" + this.oItem.ENTITYID + "/" + this.oItem.SERVICEID);
      super.subscribe("AQ/" + this.oItem.ENTITYID + "/" + this.oItem.SERVICEID);
    }
  }

