import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { NextInQComponent } from './next-in-q/next-in-q.component';
import { AvailableServiceComponent } from './available-service/available-service.component';

import { AuthGuardService } from './auth-guard.service';

const routes: Routes = [
  {
    path: '',
    component: LoginComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'nextinq',
    component: NextInQComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'availableservice',
    component: AvailableServiceComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: '**',
    redirectTo: '/login'
  }];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
